/* eslint-disable react/jsx-props-no-spreading */
/* eslint-disable no-use-before-define */
/* eslint-disable react/prefer-stateless-function */
import React from 'react';
import { formatDistance } from 'date-fns';

import NotificationFactory from './notification-types/NotificationFactory';

export default function NotificationsList({
    notifications,
    showLimit,
    pinnedList,
    pinningAlowed,
    setRead,
    setPinned,
}) {
    let notificationsToRender = notifications;
    let daysAgo = ''; // Keep track of when the date distance changes between notifications

    if (showLimit > -1) {
        notificationsToRender = notificationsToRender.slice(0, showLimit);
    }

    return notificationsToRender.map((notification, i) => {
        const { pinned } = notification;
        let showDaysSince = false;
        let createDistance;

        if (!pinnedList) {
            createDistance = formatDistance(
                notification.createdDate,
                new Date(),
                {
                    addSuffix: true,
                }
            );

            // Condense any distance less than a day into 'Today'
            if (['minute', 'hour'].some((el) => createDistance.includes(el))) {
                createDistance = 'Today';
            }

            // If the distance has changed from last notification add class to show visual X days/months ago line
            if (daysAgo !== createDistance) {
                showDaysSince = true;
                daysAgo = createDistance;
            }
        }

        return (
            <li
                key={`${notification.id}`}
                className={`notification-item ${
                    pinned ? 'notification-item--pinned' : ''
                }`}
            >
                {/* If a pinned list is being printed use this heading */}
                {i === 0 && pinnedList ? (
                    <div className="notification-item__pinned">
                        {`Pinned (${notifications.length})`}
                    </div>
                ) : (
                    <> </>
                )}

                {/* If is NOT a pinned list AND the date distance has changed use this heading */}
                {!pinnedList && showDaysSince ? (
                    <div className="notification-item__distance">
                        {createDistance}
                    </div>
                ) : (
                    <> </>
                )}

                <div className="notification-item__body">
                    {/* Use a factory pattern to decide what the content of the notification looks like */}
                    <NotificationFactory
                        {...notification}
                        onClick={() => {
                            if (!notification.read) {
                                setRead(notification);
                            }
                        }}
                        onPin={() => setPinned(notification)}
                        pinningAlowed={pinningAlowed}
                    />

                    {pinningAlowed ? (
                        <button
                            type="button"
                            onClick={() => setPinned(notification)}
                            className={`notification-item__icon ${
                                pinned ? 'notification-item__icon--pinned' : ''
                            }`}
                        >
                            <svg className="svg-icon">
                                <title>
                                    {pinned ? 'Unpin' : 'Pin'} notification
                                </title>
                                <use
                                    href={pinned ? '#pin-filled' : '#pin-ghost'}
                                />
                            </svg>
                        </button>
                    ) : (
                        <></>
                    )}
                </div>
            </li>
        );
    });
}
