/* eslint-disable react/button-has-type */
/* eslint-disable react/jsx-props-no-spreading */
import React, { useState, cloneElement } from 'react';
import PropTypes from 'prop-types';

import {
    OverlayContainer,
    DismissButton,
    useOverlayTrigger,
    useOverlayPosition,
    useOverlay,
    useModal,
} from '@react-aria/overlays';
import { useButton } from '@react-aria/button';
import { useDialog } from '@react-aria/dialog';
import { FocusScope } from '@react-aria/focus';
import { mergeProps } from '@react-aria/utils';

const OTHER_OVERLAY_CONTAINER_OPEN =
    '[data-overlay-container]:not([aria-hidden="true"])';
const AIRA_HIDDEN = '[aria-hidden="true"]';

const PopoverContent = React.forwardRef(
    (
        {
            triggerRef,
            titleId,
            title,
            children,
            isOpen,
            onClose,
            className,
            ...otherProps
        },
        ref
    ) => {
        // Handle interacting outside the dialog and pressing the Escape key to close the modal.
        const { overlayProps } = useOverlay(
            {
                onClose,
                isOpen,
                isDismissable: true,
                shouldCloseOnInteractOutside: (element) => {
                    /*
                        Framework tries to cancels all events on the basis that there is an 'overlay' showing
                        which should swallow the events, except it doesnt work consistently and will allow most
                        onClick bound events though and only block other usage from the @react-aria framework
                        like other popover openers.
                    */

                    // If content within the popover has cause ANOTHER overlay to open we dont want to close
                    // this one just because its being interacted with somehow.
                    if (
                        element.closest(OTHER_OVERLAY_CONTAINER_OPEN) &&
                        !element.closest(AIRA_HIDDEN)
                    ) {
                        return false;
                    }

                    // If the click is on the popover trigger just let it through so the popover doesnt re-open itself
                    if (triggerRef.current.contains(element)) {
                        return true;
                    }

                    onClose(); // Close the popover
                    return false; // But let the click / press event cascade
                },
            },
            ref
        );

        // Hide content outside the modal from screen readers.
        const { modalProps } = useModal();

        // Get props for the dialog and its title
        const { dialogProps, titleProps } = useDialog(
            { 'aria-describedby': titleId },
            ref
        );

        return (
            <FocusScope contain restoreFocus>
                <div
                    {...mergeProps(
                        overlayProps,
                        dialogProps,
                        otherProps,
                        modalProps
                    )}
                    ref={ref}
                    className={`popover__dialog ${className}__dialog`}
                >
                    {/* Include a title only if a titleId is not provided */}
                    {titleId ? (
                        ''
                    ) : (
                        <h2
                            {...titleProps}
                            className={`popover__title ${className}__title`}
                        >
                            {title}
                        </h2>
                    )}

                    {children}
                    <DismissButton onDismiss={onClose} />
                </div>
            </FocusScope>
        );
    }
);

/**
 * Create an ARIA / WCAG compatable popover using @react-aria
 * Modified from: https://react-spectrum.adobe.com/react-aria/useOverlayTrigger.html
 *
 * @param {string} titleId - (optional) Id of an element in the children element to act as dialog title; if not provided 'title' must be.
 * @param {string} title - (optional) Title of the dialog; if not provided 'titleId' must be.
 * @param {function} trigger - Callback function to generate the trigger button for the popover
 * @param {string} className - Class to pre-pend to all internal className definitions
 * @param {string} children - Content of the Popover
 * @returns {JSX.Element}
 */
export default function Popover({
    titleId,
    title,
    placement,
    trigger,
    className,
    children,
}) {
    // Implementing useOverlayTriggerState so i dont have to add another dependency
    const [isOpen, setIsOpen] = useState(false);

    const triggerRef = React.useRef();
    const overlayRef = React.useRef();

    // Get props for the trigger and overlay. This also handles hiding the overlay when a parent element of the trigger scrolls (which invalidates the popover positioning).
    const { triggerProps, overlayProps } = useOverlayTrigger(
        { type: 'dialog' },
        {
            isOpen,
        },
        triggerRef
    );

    // Get popover positioning props relative to the trigger
    const { overlayProps: positionProps } = useOverlayPosition({
        targetRef: triggerRef,
        overlayRef,
        placement: placement || 'bottom right',
        offset: 5,
        isOpen,
    });

    // Let the CSS and template decide the correct z-index
    delete positionProps.style.zIndex;

    // Move properties into CSS variables to make altering them easier
    // eslint-disable-next-line no-restricted-syntax
    for (const [key, value] of Object.entries(positionProps.style)) {
        // eslint-disable-next-line no-restricted-globals
        positionProps.style[`--popover-${key}`] = !isNaN(value)
            ? `${value}px`
            : value;

        delete positionProps.style[key];
    }

    const { buttonProps } = useButton(
        {
            onPress: () => setIsOpen(true),
        },
        triggerRef
    );

    // Get the trigger element from the parent
    const triggerElement = trigger({
        props: { ...mergeProps(buttonProps, triggerProps) },
        ref: triggerRef,
    });

    return (
        <>
            {triggerElement}
            {isOpen && (
                <OverlayContainer className={`popover ${className}`}>
                    <PopoverContent
                        {...overlayProps}
                        {...positionProps}
                        ref={overlayRef}
                        triggerRef={triggerRef}
                        titleId={titleId}
                        title={title}
                        isOpen
                        onClose={() => {
                            setIsOpen(false);
                        }}
                        className={className}
                    >
                        {/* Clone the children so we can pass the closeModal function to them */}
                        {cloneElement(children, {
                            closeModal: () => setIsOpen(false),
                        })}
                    </PopoverContent>
                </OverlayContainer>
            )}
        </>
    );
}

Popover.propTypes = {
    titleId: PropTypes.string,
    title: PropTypes.string,
    trigger: PropTypes.func,
    className: PropTypes.string,
};

Popover.defaultProps = {
    titleId: '',
    title: 'popover',
    trigger: ({ props, ref }) => {
        return (
            <button {...props} ref={ref}>
                Open popover
            </button>
        );
    },
    className: 'popover',
};
