/* eslint-disable react/jsx-props-no-spreading */
import React, { useEffect } from 'react';
import { formatDistance } from 'date-fns';

import uuidv4 from 'PlugAndPlay/_global/js/uuid';
import Modal from '../../../modal/jsx/modal';
import useDivButtonModalOpen from '../../../modal/jsx/useDivButtonModalOpen';

export default function Announcement(args) {
    const { title, course, body, read, onClick } = args;
    const { openModalRef, modalOpenProps, isModalOpen, setIsModalOpen } =
        useDivButtonModalOpen();

    useEffect(() => {
        // When modal is opened pass the 'click' back up to the parent
        if (isModalOpen) {
            onClick();
        }
    }, [isModalOpen]);

    return (
        <>
            <div
                {...modalOpenProps}
                ref={openModalRef}
                className={`notification-content ${
                    !read ? 'notification-content--unread' : ''
                }`}
            >
                <div className="notification-content__header">
                    <svg aria-hidden="true" className="svg-icon">
                        <use href="#edify-announcement" />
                    </svg>
                    <h2 className="notification-content__title">
                        {course ? `${course} | ` : ''}Announcement
                    </h2>
                </div>
                <div className="notification-content__body">
                    <div className="notification-content__subtitle">
                        {title}
                    </div>
                    <div className="notification-content__description">
                        {body}
                    </div>
                </div>
            </div>
            {isModalOpen && (
                <AnnouncementModal
                    {...args}
                    handleModalClose={() => {
                        setIsModalOpen(false);
                    }}
                />
            )}
        </>
    );
}

export function AnnouncementModal({
    type,
    title,
    course,
    createdDate,
    body,
    pinned,
    handleModalClose,
    onPin,
    pinningAlowed,
}) {
    let createDistance = formatDistance(createdDate, new Date(), {
        addSuffix: true,
    });

    // Condense any distance less than a day into 'Today'
    if (['minute', 'hour'].some((el) => createDistance.includes(el))) {
        createDistance = 'Today';
    }

    const titleId = uuidv4(); // Modal needs a id for the dialog described by
    return (
        <Modal titleId={titleId} onClose={handleModalClose}>
            <div
                className={`notification-content-modal notification-content-modal--${type}`}
            >
                <header className="notification-content-modal__header">
                    <svg aria-hidden="true" className="svg-icon">
                        <use href="#edify-announcement" />
                    </svg>
                    <h2
                        id={titleId}
                        className="notification-content-modal__title"
                    >
                        {course ? `${course} | ` : ''} Announcement
                    </h2>
                </header>

                <div className="notification-content-modal__body">
                    <div className="notification-content-modal__date-distance">
                        {createDistance}
                    </div>
                    {title ? (
                        <div className="notification-content-modal__subtitle">
                            {title}
                        </div>
                    ) : (
                        <></>
                    )}
                    <div className="notification-content-modal__description">
                        {body}
                    </div>
                </div>

                {pinningAlowed && (
                    <footer className="notification-content-modal__footer">
                        <button
                            type="button"
                            onClick={onPin}
                            className="notification-content-modal__pin"
                        >
                            <svg aria-hidden="true" className="svg-icon">
                                <use href="#pin" />
                            </svg>
                            {pinned ? 'Unpin it' : 'Pin it'}
                        </button>
                    </footer>
                )}
            </div>
        </Modal>
    );
}
