import React, { useState, useEffect, useContext, useCallback } from 'react';
import { OverlayProvider } from '@react-aria/overlays';
import uuidv4 from 'PlugAndPlay/_global/js/uuid';

import NotificationsContext from './data/NotificationsContext';
import NotificationList from './notificationList';
import Popover from '../../popover/jsx/popover';

export default function Notifications({
    popover,
    title,
    enablePinnedNotifications,
    showUnreadIndicator,
    notificationsLimit,
    pinnedLimit,
}) {
    const {
        notifications,
        getNotifications,
        isMoreNotifications,

        pinnedNotifications,
        getPinnedNotifications,
        setPinned,

        unreadNotificationsCount,
        getUnreadNotificationsCount,
        setRead,
    } = useContext(NotificationsContext);

    const [showAllPinned, setShowAllPinned] = useState(false);

    // Initial data load
    useEffect(() => {
        // If pinning notifications is enabled
        if (enablePinnedNotifications) {
            getPinnedNotifications();
        }
        if (showUnreadIndicator) {
            getUnreadNotificationsCount();
        }
        getNotifications({
            max: notificationsLimit,
            includePinned: !enablePinnedNotifications,
        });
    }, [enablePinnedNotifications, notificationsLimit]);

    // Handlers for data change
    const handleSetRead = useCallback(
        (notification) => {
            setRead({
                ...notification,
                read: true,
            });
        },
        [setRead]
    );
    const handleSetPinned = useCallback(
        (notification) => {
            setPinned({
                ...notification,
                pinned: !notification.pinned,
            });
        },
        [setPinned]
    );

    // All pinned notification are loaded up front but not all are shown, toggle them all visible or not
    const handlePinnedShowMore = useCallback(() => {
        setShowAllPinned(!showAllPinned);
    }, [setShowAllPinned, showAllPinned]);

    // The main notifications are paged based on the 'notificationsLimit input, grab the next page worth of notifications
    const handleGetMoreNotifications = useCallback(() => {
        getNotifications({
            start: notifications.length,
            max: notificationsLimit,
            includePinned: !enablePinnedNotifications,
        });
    }, [notifications, notificationsLimit, enablePinnedNotifications]);

    const titleId = uuidv4(); // Popover needs a id for the dialog described by
    function NotificationsContentInner({ closeModal }) {
        return (
            <div className="no-wysiwyg notifications">
                <div className="notifications__header">
                    <h1 id={titleId} className="notifications__title">
                        {title}
                    </h1>
                    <button
                        className="notifications__header-button"
                        type="button"
                        onClick={closeModal}
                    >
                        <span className="sr-only">Close</span>
                        <svg className="notifications__header-button-icon">
                            <use href="#close" />
                        </svg>
                    </button>
                </div>

                <div className="notifications__body">
                    {enablePinnedNotifications &&
                        pinnedNotifications.length > 0 && (
                            <ul
                                aria-label="Pinned notifications"
                                className="notifications__list"
                            >
                                <NotificationList
                                    notifications={pinnedNotifications}
                                    showLimit={showAllPinned ? -1 : pinnedLimit}
                                    showMore={handlePinnedShowMore}
                                    pinnedList
                                    pinningAlowed
                                    setRead={handleSetRead}
                                    setPinned={handleSetPinned}
                                />
                            </ul>
                        )}

                    {enablePinnedNotifications &&
                        pinnedNotifications.length > pinnedLimit && (
                            <button
                                type="button"
                                onClick={handlePinnedShowMore}
                                className="notifications__expand-pinned"
                            >
                                <svg className="svg-icon">
                                    <use href="#add" />
                                </svg>
                                Show {showAllPinned ? 'less' : 'more'} (
                                {pinnedNotifications.length - pinnedLimit})
                            </button>
                        )}

                    <ul
                        aria-label="Notifications"
                        className="notifications__list"
                    >
                        <NotificationList
                            notifications={notifications}
                            pinningAlowed={enablePinnedNotifications}
                            setRead={handleSetRead}
                            setPinned={handleSetPinned}
                        />
                    </ul>

                    {isMoreNotifications && (
                        <button
                            type="button"
                            onClick={handleGetMoreNotifications}
                            className="notifications__more"
                        >
                            <svg className="svg-icon">
                                <use href="#add" />
                            </svg>
                            Show more
                        </button>
                    )}
                </div>
            </div>
        );
    }

    let notificationsContent = null;

    if (popover) {
        notificationsContent = (
            <Popover
                titleId={titleId}
                trigger={({ props, ref }) => {
                    return (
                        <button
                            ref={ref}
                            {...props}
                            type="button"
                            className="notifications-wrapper__action"
                        >
                            {showUnreadIndicator &&
                                unreadNotificationsCount > 0 && (
                                    <div
                                        aria-label="unread notifications"
                                        className="notifications-wrapper__unread"
                                    >
                                        {unreadNotificationsCount}
                                    </div>
                                )}
                            <svg className="svg-icon">
                                <title>View notifications</title>
                                <use href="#notification" />
                            </svg>
                        </button>
                    );
                }}
                className="notification-popover"
            >
                <NotificationsContentInner />
            </Popover>
        );
    }

    return (
        <OverlayProvider className="notifications-wrapper">
            {notificationsContent !== null ? (
                notificationsContent
            ) : (
                <NotificationsContentInner />
            )}
        </OverlayProvider>
    );
}
